import React, { useState } from 'react';
import {Table} from 'react-bootstrap';
import { connect } from 'react-redux';

function Cart(props){

    let [clos, closUpd] = useState(true)

    return (
        <div>
            <Table responsive>
            <tr>
                <th>#</th>
                <th></th>
                <th></th>
                <th>(+)</th>
                <th>(-)</th>
            </tr>
            { 
                props.state.map((a,i)=>{
                    return (
                        <tr key={i}>
                            <td>{a.id}</td>
                            <td>{a.name}</td>
                            <td>{a.quan}</td>
                            <td><button onClick={()=>{ props.dispatch({ type : 'quanPlus' , index : a.id }) }}>+</button></td>
                            <td><button onClick={()=>{ props.dispatch({ type : 'quanMinus', index : a.id }) }}>-</button></td>
                        </tr>
                    )
                })  
            }
            </Table>

            {
                clos === true
                ? (<div className="my_alert">
                    <p>   20%</p>
                    <button onClick={ () => { closUpd(false) } }></button>
                </div>)
                : null
            }
            
        </div>
    )
}

function cartState(state){

    return {
        state : state.reducer,
        alertOpen : state.reducer2
    }
}

export default connect(cartState)(Cart)
