import logo from './logo.svg';
import { Navbar, Container, Nav, NavDropdown, Jumbotron, Button } from 'react-bootstrap';
import React, { useContext, useState, lazy, Suspense } from 'react';
import { Link, Route, Routes, useNavigate} from 'react-router-dom';

import DataTemp from './component/data.js';
import './App.css';
import Cart from './Cart.js'
//import DetailPage from './component/Detail.js';

import axios from 'axios';
import { Provider } from 'react-redux';

let DetailPage = lazy(()=>{ return import('./component/Detail.js') });

export let stokecontext = React.createContext();
let appstoke = React.createContext();

function App() {

  let [shoes, shoesUpd] = useState(DataTemp);
  let [stoke, stokeUpd] = useState([10, 11, 12]);
  
  return (
    <div className="App">
        <Navbar collapseOnSelect expand="lg">
        <Container>
        <Navbar.Brand href="#">ShoesShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail"></Nav.Link>
            <Nav.Link href="#pricing">2</Nav.Link>
            <NavDropdown title="3" id="collasible-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link href="#deets">4</Nav.Link>
            <Nav.Link eventKey={2} href="#memes">
            5
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
        </Container>
      </Navbar>           

        <Routes>
          <Route path="/" element={
            <div>
              <div className="background">
                <h1 className="margin">20% Season Sale</h1>
                <p>
                  This is a simple hero unit, a simple jumbotron-style component for calling extra attention to featured content or information.
                </p>
                <p>
                  <Button variant="primary">Learn more</Button>
                </p>
              </div>    

              <div className="container">

                  <appstoke.Provider value={stoke}>

                    <div className="row">
                        { 
                          shoes.map((a,i)=>{
                          return <Card shoes={shoes[i]} i={i} key={i}/>
                          })
                        }
                    </div>

                  </appstoke.Provider>

                  <button className="btn btn-primary" onClick={()=>{ 
                  axios.get('https://codingapple1.github.io/shop/data2.json')
                  .then((result)=>{
                    shoesUpd( [...shoes, ...result.data] );
                  })
                  .catch(()=>{
                    console.log("")
                  })

                }} ></button>
            </div>
          </div>}
          />

            <Route path="/detail/:id" element={ 
              <Suspense fallback={<div>.</div>}>
                <DetailPage shoes={shoes}  stoke={stoke} stokeUpd={stokeUpd} />
              </Suspense>
            } />

          <Route path="/cart" element={<Cart />} />

        </Routes>
    </div>
  );
}

function Card(props){

  let navigate = useNavigate();
  let cardStoke = useContext(stokecontext);

  return (
    <div className="col-md-4" onClick={ ()=>{ navigate('/detail/'+ ( props.shoes.id )) } }>
      <img src={ 'https://codingapple1.github.io/shop/shoes' + (props.i+1) + '.jpg' } width="100%" alt={(props.i+1)} />
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.content }</p>

    </div>
  )
}

export default App;
