import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import serviceWorkerRegistration from './serviceWorkerRegistration';
import { BrowserRouter } from 'react-router-dom';

import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';


function reducer2(state = true, act)
{
    if (act.type === "Close")
    {
        return false;

    }
    else
    {
        return state;
    }
}

let baseState = [
];

//  state     
// state = baseState [ default  ES6]
// [...value] deep Copy
function reducer(state = baseState, act){

  if ( act.type === "quanAdd" )
  {

    let fIdx = state.findIndex( (a)=>{ console.log("a.id " + a.id); return (a.id) === act.data.id} );
    console.log(act.data.id);
    console.log(fIdx);
    
    if ( fIdx >= 0 )
    {
        let baseStateTmp = [...state];
        baseStateTmp[fIdx].quan++;
        state++;
        return baseStateTmp;
    }
    else
    {
        let baseStateTmp = [...state];
        baseStateTmp.push(act.data);
        return baseStateTmp;
    }
  }
  else if ( act.type === 'quanPlus' ){

    let baseStateTmp = [...state];
    baseStateTmp[act.index].quan++;
    return baseStateTmp

  } else if ( act.type === 'quanMinus' ){

    let baseStateTmp = [...state];
    baseStateTmp[act.index].quan--;
    return baseStateTmp

  } else {
    return state
  }

}

// combineReducers  reducer 
let store = createStore(combineReducers({reducer, reducer2}))

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
